import styled from "styled-components";
import { useEffect } from "react";
import gsap from "gsap";

export default function StaggeredAnimation(){

useEffect(()=>{
gsap.to(".box",{
    scale: 0.1, 
    y: 90,
    yoyoEase: true,
    repeat: -1, 
    ease: "back",
    delay:1,
    stagger: {
      amount: -0.5, 
      grid: "auto",
      from: "right"
      }
})

})


const BoxContainer = styled.div`
display:grid;
grid-template-columns: 90fr 90fr 90fr;
grid-template-rows: 90fr 90fr 90fr;
width:500px;
height:250px;
justify-content: center;
margin-top:50px;
padding:50px;
margin-left:35%;

`
const InnerBox = styled.div`
width:50px;
height:50px;
border-radius:45px;
background-color:orange;
margin:2px;
`

return (
<BoxContainer className="boxContainer">
    <InnerBox className="box"></InnerBox>
    <InnerBox className="box" ></InnerBox>
    <InnerBox className="box"></InnerBox>
    <InnerBox className="box"></InnerBox>
    <InnerBox className="box"></InnerBox>
    <InnerBox className="box"></InnerBox>
    <InnerBox className="box"></InnerBox>
    <InnerBox className="box"></InnerBox>
    <InnerBox className="box"></InnerBox>
    <InnerBox className="box"></InnerBox>
    <InnerBox className="box"></InnerBox>
    <InnerBox className="box"></InnerBox>
    <InnerBox className="box"></InnerBox>
    <InnerBox className="box"></InnerBox>
    <InnerBox className="box"></InnerBox>
   
   
</BoxContainer>

)

}